import { sanityClient } from "@/lib/sanity";
import { PortableText } from "@portabletext/react";
import Footer from "@/components/footer";
import { notFound } from "next/navigation";

export const dynamicParams = true; // allow on-demand slugs in dev / when new posts are added

/* ----------------------------------------
   GROQ QUERY
---------------------------------------- */
const POST_QUERY = `
*[_type == "post" && slug.current == $slug][0]{
  title,
  excerpt,
  publishedAt,

  mainImage{
    alt,
    asset->{ url }
  },

  author->{ name },

  content,

  seo{
    metaTitle,
    metaDescription
  }
}
`;

/* ----------------------------------------
   FETCH HELPER
---------------------------------------- */
async function getPost(slug) {
  if (!slug) {
    console.error("getPost called with empty slug");
    return null;
  }

  try {
    console.log("Fetching post for slug:", slug);
    const post = await sanityClient.fetch(POST_QUERY, { slug });
    console.log("Sanity result for slug:", slug, !!post);
    return post;
  } catch (err) {
    console.error("Error fetching post from Sanity:", err);
    return null;
  }
}

/* ----------------------------------------
   STATIC PARAMS (optional â€” kept for build-time SSG)
---------------------------------------- */
export async function generateStaticParams() {
  const slugs = await sanityClient.fetch(`
    *[_type == "post" && defined(slug.current)]{
      "slug": slug.current
    }
  `);

  console.log("ðŸ”¥ STATIC PARAMS SLUGS:", slugs);

  return (slugs || []).map(item => ({
    slug: item.slug,
  }));
}

/* ----------------------------------------
   METADATA
---------------------------------------- */
export async function generateMetadata({ params }) {
  const rawSlug = params?.slug;
  const slug = Array.isArray(rawSlug) ? rawSlug.join("/") : rawSlug;

  if (!slug) return {};

  const post = await getPost(slug);
  if (!post) return {};

  return {
    title: post.seo?.metaTitle || post.title,
    description: post.seo?.metaDescription || post.excerpt,
  };
}

/* ----------------------------------------
   READ TIME
---------------------------------------- */
function calculateReadTime(content) {
  if (!content || !Array.isArray(content)) return "0 min read";

  const text = content
    .filter(block => block._type === "block")
    .map(block =>
      (block.children || [])
        .map(child => child?.text || "")
        .join(" ")
    )
    .join(" ")
    .trim();

  if (!text) return "0 min read";

  const words = text.split(/\s+/).filter(Boolean).length;
  return `${Math.ceil(words / 200)} min read`;
}

/* ----------------------------------------
   PORTABLE TEXT
---------------------------------------- */
const portableTextComponents = {
  types: {
    image: ({ value }) => {
      const src = value?.asset?.url || "";
      const alt = value?.alt || "";
      if (!src) return null;
      return (
        <figure className="my-10">
          <img src={src} alt={alt} className="w-full rounded-2xl" />
        </figure>
      );
    },
    code: ({ value }) => (
      <pre className="bg-gray-900 text-gray-100 rounded-xl p-5 overflow-x-auto my-8">
        <code>{value?.code || ""}</code>
      </pre>
    ),
  },
};

/* ----------------------------------------
   PAGE
---------------------------------------- */
export default async function BlogPostPage({ params }) {
  // log params early so you can see the shape when debugging
  console.log("BlogPostPage params:", params);

  const rawSlug = params?.slug;
  const slug = Array.isArray(rawSlug) ? rawSlug.join("/") : rawSlug;

  if (!slug) {
    console.error("Missing slug in params:", params);
    // prefer 404 if route doesn't include a slug; helps avoid calling getPost with undefined
    notFound();
  }

  const post = await getPost(slug);

  if (!post) {
    console.error(`Post not found for slug: ${slug}`);
    notFound();
  }

  const readTime = calculateReadTime(post.content);

  return (
    <main className="max-w-4xl mx-auto px-5 py-12">
      <h1 className="text-4xl md:text-5xl font-bold mb-4">
        {post.title}
      </h1>

      <div className="flex gap-4 text-sm text-gray-500 mb-8">
        {post.author?.name && <span>By {post.author.name}</span>}
        {post.publishedAt && (
          <span>{new Date(post.publishedAt).toLocaleDateString()}</span>
        )}
        <span>{readTime}</span>
      </div>

      {post.mainImage?.asset?.url && (
        <img
          src={post.mainImage.asset.url}
          alt={post.mainImage.alt || post.title}
          className="w-full rounded-3xl mb-12"
        />
      )}

      <article className="prose prose-lg max-w-none">
        <PortableText
          value={post.content}
          components={portableTextComponents}
        />
      </article>

      <div className="mt-24">
        <Footer />
      </div>
    </main>
  );
}